<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="info" :closable="false" show-icon center> </el-alert>

      <!--  -->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center="">
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!--  -->
      <el-form :model="addForm" :rules="addFormRule" ref="addFormRef" label-width="100px" label-position="top">
        <!--  -->
        <el-tabs v-model="activeIndex" :tab-position="'left'" :before-leave="beforeTabsLeave" @tab-click="tabClick">
          <!--  -->
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader v-model="addForm.goods_cat" :options="cateList" expandTrigger="hover" :props="cateProp" @change="handleChange" clearable></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!-- item -->
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="cb" v-for="(cb, i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload :action="uploadUrl" :on-preview="handlePreview" :on-remove="handleRemove" list-type="picture" :headers="headersObj" :on-success="handleSuccess">
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="addForm.goods_introduce" />
            <el-button type="primary" class="addBtn" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>

      <!--  -->
      <el-dialog title="" :visible.sync="previewDialogVisible" width="50%">
        <img :src="preview" alt="" class="previewImg" />
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
// lodash 
import _ from 'lodash'

export default {
  //  attr_valsid
  data() {
    return {
      activeIndex: '0',
      // 
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        // 
        goods_cat: [],
        // 
        pics: [],
        // 
        goods_introduce: '',
        // 
        attrs: []
      },
      // 
      addFormRule: {
        goods_name: [{ required: 'ture', message: '!', trigger: 'blur' }],
        goods_price: [{ required: 'ture', message: '!', trigger: 'blur' }],
        goods_weight: [{ required: 'ture', message: '!', trigger: 'blur' }],
        goods_number: [{ required: 'ture', message: '!', trigger: 'blur' }]
      },
      // 
      cateList: [],
      // 
      cateProp: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      cateForm: [],
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      uploadUrl: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 
      headersObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      preview: '',
      // 
      previewDialogVisible: false
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.cateList = res.data
      console.log(res)
    },
    // 
    handleChange() {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    // 
    beforeTabsLeave(activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    // 
    async tabClick() {
      // 1 
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: 'many' } })

        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }

        res.data.forEach(item => {
          item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })

        this.manyTableData = res.data
      } else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: 'only' } })

        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }

        console.log(res)
        this.onlyTableData = res.data
      }
    },
    // 
    handlePreview(file) {
      this.preview = file.response.data.url
      this.previewDialogVisible = true
    },
    // 
    handleRemove(file) {
      // 1.
      const filePath = file.response.data.tmp_path
      // 2.pics 
      const i = this.addForm.pics.findIndex(x => x.pic === filePath)
      // 3.  splice 
      this.addForm.pics.splice(i, 1)
    },
    // 
    handleSuccess(response) {
      // 
      const picInfo = { pic: response.data.tmp_path }
      //   pics 
      this.addForm.pics.push(picInfo)
      console.log(this.addForm)
    },
    // 
    add() {
      this.$refs.addFormRef.validate(async validate => {
        if (!validate) {
          return this.$message.error('')
        }
        // 
        // lodashcloneDeep
        const form = _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')

        // 
        this.manyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals.join(' ') }
          this.addForm.attrs.push(newInfo)
        })

        // 
        this.onlyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
          this.addForm.attrs.push(newInfo)
        })

        //   
        form.atrrs = this.addForm.attrs

        // 
        const { data: res } = await this.$http.post('goods', form)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    // id
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}

.previewImg {
  width: 100%;
}

.addBtn {
  margin-top: 15px;
}
</style>
