<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title=": " type="warning" :closable="false" show-icon> </el-alert>
      <!--  -->
      <el-row class="cat_opt">
        <el-col style="margin:15px 0 25px 0">
          <span style="margin-right:15px">:</span>
          <el-cascader v-model="selectedCateKeys" :options="catelist" expandTrigger="hover" :props="cateProp" @change="handleChange"></el-cascader>
        </el-col>
      </el-row>
      <!-- tab  -->
      <!-- v-model   @tab-click tab-->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane label="" name="many">
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true"></el-button>
          <!--  -->
          <el-table :data="manyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <!--  -->
              <template slot-scope="scrope">
                <el-tag v-for="(item, i) in scrope.row.attr_vals" :key="i" closable @close="handleClose(i, scrope.row)">{{ item }}</el-tag>
                <!--  -->
                <el-input class="input-new-tag" v-if="scrope.row.inputVisible" v-model="scrope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scrope.row)" @blur="handleInputConfirm(scrope.row)"> </el-input>
                <!--  -->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scrope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scrope">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditvisible(scrope.row.attr_id)"></el-button>
                <el-button type="warning" icon="el-icon-delete" size="mini" @click="deleteParams(scrope.row.attr_id)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true"></el-button>
          <!--  -->
          <el-table :data="onlyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <!--  -->
              <template slot-scope="scrope">
                <el-tag v-for="(item, i) in scrope.row.attr_vals" :key="i" closable @close="handleClose(i, scrope.row)">{{ item }}</el-tag>
                <!--  -->
                <el-input class="input-new-tag" v-if="scrope.row.inputVisible" v-model="scrope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scrope.row)" @blur="handleInputConfirm(scrope.row)"> </el-input>
                <!--  -->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scrope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scrope">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditvisible(scrope.row.attr_id)"></el-button>
                <el-button type="warning" icon="el-icon-delete" size="mini" @click="deleteParams(scrope.row.attr_id)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>

      <!-- / -->
      <el-dialog :title="'' + titleText" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
        <!--  -->
        <el-form :model="addForm" :rules="addFormRule" ref="addFormRef" label-width="100px">
          <el-form-item :label="titleText" prop="attr_name">
            <el-input v-model="addForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addParams"> </el-button>
        </span>
      </el-dialog>

      <!-- / -->
      <el-dialog :title="'' + titleText" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
        <!--  -->
        <el-form :model="editForm" :rules="editFormRule" ref="editFormRef" label-width="100px">
          <el-form-item :label="titleText" prop="attr_name">
            <el-input v-model="editForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editParams"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      catelist: [],
      // 
      cateProp: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // id
      selectedCateKeys: [],
      // 
      activeName: 'many',
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      addDialogVisible: false,
      // 
      addForm: {
        attr_name: ''
      },
      // 
      addFormRule: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRule: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created() {
    // 
    this.getCateList()
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.catelist = res.data
    },
    //  
    handleChange() {
      // 
      this.getParamsList()
    },
    // tab
    handleTabClick() {
      // 
      this.getParamsList()
    },
    // 
    async getParamsList() {
      // 
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = []
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      //  
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: this.activeName } })
      if (res.meta.status !== 200) {
        return this.$message.info('')
      }
      // atter_vals   split
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 
        item.inputVisible = false
        // 
        item.inputValue = ''
      })
      // this.$message.success('')
      //  
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    //  
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addParams() {
      this.$refs.addFormRef.validate(async validate => {
        if (!validate) return
        const { data: res } = await this.$http.post(`categories/${this.cateId}/attributes`, { attr_name: this.addForm.attr_name, attr_sel: this.activeName })
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getParamsList()
        this.addDialogVisible = false
      })
    },
    // 
    async showEditvisible(id) {
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes/${id}`, { params: { attr_sel: this.activeName } })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editParams() {
      this.$refs.editFormRef.validate(async validate => {
        if (!validate) return
        // 
        const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`, { attr_name: this.editForm.attr_name, attr_sel: this.activeName })
        if (res.meta.status !== 200) {
          return this.$message.error('' + this.titleText + '')
        }
        this.$message.success('' + this.titleText + '')
        this.getParamsList()
        this.editDialogVisible = false
      })
    },
    // 
    async deleteParams(id) {
      const { data: res } = await this.$http.delete(`categories/${this.cateId}/attributes/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('' + this.titleText + '')
      }
      this.$message.success('' + this.titleText + '')
      this.getParamsList()
    },
    // enter
    async handleInputConfirm(row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      // return 
      //  
      // 
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      //  
      this.saveAttrVals(row)
    },
    //  
    async saveAttrVals(row) {
      //  
      const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, { attr_name: row.attr_name, attr_sel: row.attr_sel, attr_vals: row.attr_vals.join(' ') })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
    },
    // 
    showInput(row) {
      row.inputVisible = true
      // 
      // $nextTick  
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 
    async handleClose(i, row) {
      // i
      row.attr_vals.splice(i, 1)
      this.saveAttrVals(row)
    }
  },
  computed: {
    //  ture false
    isBtnDisabled() {
      if (this.selectedCateKeys.length !== 3) {
        return true
      } else {
        return false
      }
    },
    // id
    cateId() {
      return this.selectedCateKeys[2]
    },
    // 
    titleText() {
      if (this.activeName === 'many') {
        return ''
      } else {
        return ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
.cat_opt {
  margin: 15pxx;
}
.input-new-tag {
  width: 120px;
}
</style>
