<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCate"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <tree-table class="treeTable" :data="Catelist" :columns="columns" :selection-type="false" :expand-type="false" :show-index="true" index-text="#" border>
        <!--   -->
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i class="el-icon-success font-size" v-if="scope.row.cat_deleted === false" style="color:lightgreen"></i>
          <i class="el-icon-error font-size" v-else style="color:red"></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag type="success" v-else-if="scope.row.cat_level === 1"></el-tag>
          <el-tag type="warning" v-else></el-tag>
        </template>
        <!-- -->
        <template slot="option" slot-scope="scope">
          <el-button icon="el-icon-edit" type="warning" size="mini" plain @click="showCateEdit(scope.row.cat_id)"></el-button>
          <el-button icon="el-icon-delete" type="danger" size="mini" plain @click="deleteCate(scope.row.cat_id)"></el-button>
        </template>
      </tree-table>

      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[5, 8, 12]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total"> </el-pagination>

      <!--  -->
      <el-dialog title="" :visible.sync="addCateDialogVisible" width="40%" @close="addCateDialogClosed">
        <!--  -->
        <el-form :model="addCateForm" :rules="addCateFormRule" ref="addCateFormRef" label-width="100px">
          <el-form-item label="" prop="cat_name">
            <el-input v-model="addCateForm.cat_name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <!-- option  -->
            <!-- props  -->
            <!-- v-model  id -->
            <el-cascader expandTrigger="hover" v-model="selectedKeys" :options="parentCateList" :props="cascaderPorps" @change="parentCateChanged" clearable></el-cascader>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addCateDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addCate"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="cateEditDialogVisible" width="40%">
        <!--  -->
        <el-form :model="cateEditInfoForm" :rules="cateEditInfoRule" ref="cateEditInfoRef" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="cat_name">
            <el-input v-model="cateEditInfoForm.cat_name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="cat_level">
            <el-input v-model="cateEditInfoForm.cat_level"></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer">
          <el-button @click="dialogVisibcateEditDialogVisiblele = false"> </el-button>
          <el-button type="primary" @click="saveCateEditInfo"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      queryInfo: {
        type: 3,
        // 
        pagenum: 1,
        // 
        pagesize: 5
      },
      // 
      Catelist: [],
      // 
      total: 0,
      // table
      columns: [
        { label: '', prop: 'cat_name' },
        // type    template 
        { label: '', type: 'template', template: 'isok' },
        // 
        { label: '', type: 'template', template: 'order' },
        // 
        { label: '', type: 'template', template: 'option' }
      ],
      addCateDialogVisible: false,
      addCateForm: {
        // 
        cat_name: '',
        // id 1
        cat_pid: 0,
        //  1
        cat_level: 0
      },
      // 
      addCateFormRule: {
        cat_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      parentCateList: [],
      // id
      selectedKeys: [],
      // 
      cascaderPorps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        checkStrictly: 'true'
      },
      cateEditDialogVisible: false,
      cateEditInfoForm: [],
      cateEditInfoRule: {
        cat_name: [{ message: '', trigger: 'blur' }],
        cat_level: [{ required: true, message: '', trigger: 'blur' }, { message: '0~2 0 1 2' }]
      }
    }
  },
  created() {
    this.getCatelist()
  },
  methods: {
    async getCatelist() {
      const { data: res } = await this.$http.get('categories', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.Catelist = res.data.result
      this.total = res.data.total
      console.log(res)
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCatelist()
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCatelist()
    },
    // 
    showAddCate() {
      this.getParentCateList()
      this.addCateDialogVisible = true
    },
    // 
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', { params: { type: 2 } })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.parentCateList = res.data
    },
    // 
    parentCateChanged() {
      // selectKeys  length0 
      //  
      if (this.selectedKeys.length > 0) {
        // id
        this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
        // 
        this.addCateForm.cat_level = this.selectedKeys.length
      } else {
        // id  
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    // 
    addCate() {
      this.$refs.addCateFormRef.validate(async validate => {
        if (!validate) return
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getCatelist()
        this.addCateDialogVisible = false
      })
    },
    // 
    addCateDialogClosed() {
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
    },
    //  id  
    async showCateEdit(id) {
      const { data: res } = await this.$http.get('categories/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.cateEditInfoForm = res.data
      this.cateEditDialogVisible = true
    },
    // 
    saveCateEditInfo() {
      this.$refs.cateEditInfoRef.validate(async validate => {
        if (!validate) return
        const { data: res } = await this.$http.put('categories/' + this.cateEditInfoForm.cat_id, this.cateEditInfoForm)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getCatelist()
        this.cateEditDialogVisible = false
      })
    },
    async deleteCate(id) {
      const { data: res } = await this.$http.delete('categories/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getCatelist()
    }
  }
}
</script>

<style lang="less" scoped>
.treeTable {
  margin-top: 15px;
  font-size: 13px;
}
.el-cascader {
  width: 100%;
}
.font-size {
  font-size: 16px;
}
</style>
