<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!-- / -->
      <el-row :gutter="20">
        <!--  -->
        <el-col :span="8">
          <!--   -->
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodsList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <!--  -->
        <el-col :span="4">
          <el-button type="primary" @click="goAppPage"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="goodslist" border stripe>
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="goods_name"></el-table-column>
        <el-table-column label="" prop="goods_price" width="95px"></el-table-column>
        <el-table-column label="" prop="goods_weight" width="95px"></el-table-column>
        <el-table-column label="" prop="add_time" width="170px">
          <template slot-scope="scope">{{ scope.row.add_time | dateFormat }}</template>
        </el-table-column>
        <el-table-column label="" width="160px">
          <template slot-scope="scope">
            <el-button type="warning" icon="el-icon-edit" size="mini" circle @click="editGoods(scope.row.goods_id)"></el-button>

            <el-button type="danger" icon="el-icon-delete" size="mini" circle @click="deleteGoods(scope.row.goods_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[5, 10, 20, 25]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total"> </el-pagination>

      <!--  -->
      <el-dialog title="" :visible.sync="editDialogVisibled" width="50%" @close="editDialogClosed">
        <el-form :model="editForm" :rules="editFormRule" ref="editFormRef" label-width="90px">
          <el-form-item label="" prop="goods_name">
            <el-input v-model="editForm.goods_name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_price">
            <el-input v-model="editForm.goods_price"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_weight">
            <el-input v-model="editForm.goods_weight"></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisibled = false"> </el-button>
          <el-button type="primary" @click="editSave"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      goodslist: [],
      // 
      total: 0,
      // 
      editForm: [],
      // 
      editDialogVisibled: false,
      // 
      editFormRule: {
        goods_name: [{ required: 'true', message: '', blur: 'true' }],
        goods_price: [{ required: 'true', message: '', blur: 'true' }],
        goods_weight: [{ required: 'true', message: '', blur: 'true' }]
      }
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    // 
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // this.$message.success('')
      console.log(res)
      this.goodslist = res.data.goods
      this.total = res.data.total
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getGoodsList()
    },
    //  
    handleCurrentChange(nwePage) {
      this.queryInfo.pagenum = nwePage
      this.getGoodsList()
    },
    // 
    async deleteGoods(id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      // 
      const { data: res } = await this.$http.delete(`goods/${id}`)
      if (res.meta.status !== 200) {
        return this.$methods.error('')
      }
      this.$message.success('')
      this.getGoodsList()
    },
    //  
    goAppPage() {
      this.$router.push('/add')
    },
    // 
    async editGoods(id) {
      const { data: res } = await this.$http.get(`goods/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editForm = res.data
      this.editDialogVisibled = true
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editSave() {
      this.$refs.editFormRef.validate(async validate => {
        if (!validate) return

        // 
        const { data: res } = await this.$http.put('goods/ ' + this.editForm.goods_id, this.editForm)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getGoodsList()
        this.editDialogVisibled = false
      })
    }
  }
}
</script>

<style lang="less" scoped></style>
